/* ================= CONTAINER ================= */

.aeps-invoice-container {
  max-width: 880px;
  margin: 40px auto;
  padding: 25px;
  font-family: "Segoe UI", Arial, sans-serif;
  background: var(--bs-primary3);
  border-radius: 14px;
}

/* ================= CARD ================= */

.aeps-invoice {
  background: #ffffff;
  border-radius: 18px;
  padding: 45px 40px;
  box-shadow: 0 14px 35px rgba(0, 0, 0, 0.08);
  position: relative;
}

/* ================= HEADER ================= */

.invoice-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  border-bottom: 1px solid #eee;
  padding-bottom: 15px;
}

/* LOGOS */

.company-logo {
  height: 70px;
  width: auto;
  object-fit: contain;
}

.aeps-logo {
  height: 55px;
  width: auto;
  object-fit: contain;
}

/* ================= TITLE ================= */

.invoice-h1 {
  text-align: center;
  font-size: 32px;
  font-weight: 600;
  color: #193968 !important;
  margin: 25px 0 15px;
  position: relative;
}

.invoice-h1::after {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  background: #2e7d32;
  margin: 12px auto 0;
  border-radius: 5px;
}

/* ================= STATUS ================= */

.transaction-status {
  display: flex;
  justify-content: center;
  align-items: center;
  /* gap: 12px; */
  margin-bottom: 40px;
  font-weight: 600;
  font-size: 14px;
}

.status-icon {
  font-size: 20px;
}

.status {
  padding: 6px 16px;
  border-radius: 20px;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 0.5px;
}

.success {
  background: #e8f5e9;
  color: #2e7d32;
}

.failed {
  background: #ffebee;
  color: #c62828;
}

.pending {
  background: #fff8e1;
  color: #f9a825;
}
.status-success-img {
  width: 20px;
  height: 20px;
  object-fit: contain;
}


/* ================= DETAILS ================= */

.invoice-details {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;
  margin-top: 15px;
}

/* COLUMN */

.detail-column {
  display: flex;
  flex-direction: column;
}

/* Push right column outward for symmetry */
.detail-column:last-child {
  padding-left: 25px;
}

/* ITEM */

.detail-item {
  width: 100%;
  margin-bottom: 22px;
  padding-bottom: 12px;
  border-bottom: 1px solid #f1f3f5;
}

.detail-item:last-child {
  border-bottom: none;
}

/* LABEL */

.label {
  font-size: 12px;
  color: #8c9aa5;
  font-weight: 500;
  letter-spacing: 0.4px;
  text-transform: uppercase;
}

/* VALUE */

.value {
  display: block;
  margin-top: 6px;
  font-size: 16px;
  font-weight: 600;
  color: #2c3e50;
  line-height: 1.4;
  word-break: break-word;
}

/* AMOUNT */

.amount {
  font-size: 18px;
  color: #2e7d32;
  font-weight: 700;
}

/* ================= FOOTER ================= */

.invoice-footer {
  text-align: center;
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px dashed #e0e0e0;
  font-size: 14px;
  color: #666;
  line-height: 1.6;
}

.invoice-footer a {
  color: #cc9840 !important;
  font-weight: 600;
  text-decoration: none;
}

/* ================= BUTTON ================= */

.download-btn {
  display: block;
  width: 230px;
  margin: 30px auto 0;
  padding: 13px;
  background: #2e7d32;
  color: #fff;
  border: none;
  border-radius: 9px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.download-btn:hover {
  background: #1b5e20;
  transform: translateY(-1px);
}

/* ================= LOADING ================= */

.loading {
  text-align: center;
  font-size: 18px;
  color: #666;
  margin-top: 50px;
}

/* ================= RESPONSIVE ================= */

@media (max-width: 768px) {

  .aeps-invoice {
    padding: 30px;
  }

  .invoice-details {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .invoice-header {
    flex-direction: column;
    gap: 15px;
  }

  .company-logo {
    height: 60px;
  }

  .aeps-logo {
    height: 45px;
  }

}
